import { useEffect, useState } from 'react'
import { useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import Masonry from 'react-masonry-css'
import { useNavigate } from 'react-router'

import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Box,
  Button,
  Grid,
  Skeleton,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import ConfirmDialog from 'src/components/ConfirmDialog'
import MyPageReviewCard from 'src/components/MypageReviewCard'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import { useDeleteMyReview, useGetUserReviews } from 'src/hooks/api/review'

import PasswordModal from './PasswordModal'
import UserDataModal from './UserDataModal'
import WithdrawModal from './WithdrawModal'
import { type AuthStateUserObject } from 'react-auth-kit/dist/types'

const breakpointColumns = {
  default: 3,
  960: 2,
  600: 1
}

function Mypage() {
  const authUser = useAuthUser()
  const navigate = useNavigate()

  const [user, setUser] = useState<AuthStateUserObject>({})
  const [userDataModal, setUserDataModal] = useState(false)
  const [passwordModal, setPasswordModal] = useState(false)
  const [withdrawModal, setWithdrawModal] = useState(false)

  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('md'))
  const isKlubSuperAdmin = user.permission === 'SUPERADMIN'

  const { data: reviews } = useGetUserReviews()
  const deleteMyReviewMutation = useDeleteMyReview()

  const [deleteTarget, setDeleteTarget] = useState<{ reviewId: number; clubId: number } | null>(null)
  const [isDeleteConfirmOpen, setIsDeleteConfirmOpen] = useState(false)

  const handleDeleteRequest = (reviewId: number, clubId: number) => {
    setDeleteTarget({ reviewId, clubId })
    setIsDeleteConfirmOpen(true)
  }

  const handleDeleteConfirm = () => {
    if (deleteTarget) {
      deleteMyReviewMutation.mutate({ reviewId: deleteTarget.reviewId, clubId: deleteTarget.clubId })
      setIsDeleteConfirmOpen(false)
      setDeleteTarget(null)
    }
  }

  useEffect(() => setUser(authUser() ?? {}), [])

  const handleNavigateAdmin = () => {
    navigate('/admin')
  }

  return (
    <>
      <Helmet>
        <title>사용자 정보</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container alignItems="center">
          <Grid item>
            <Typography variant="h3" component="h3" gutterBottom>
              마이페이지
            </Typography>
            <Typography variant="subtitle2">KLUB에 등록된 {user?.nickname} 님의 정보입니다</Typography>
          </Grid>
        </Grid>
      </PageTitleWrapper>
      <Box sx={{ px: matches ? 3 : 0 }}>
        <Accordion defaultExpanded={true} square={!matches}>
          <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel-content" id="panel-header">
            <Typography variant="h4" component="h4">
              사용자 정보
            </Typography>
          </AccordionSummary>
          <AccordionDetails id="panel-content">
            <Grid container spacing={2}>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>닉네임</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {user ? (
                  <Typography>{user.nickname}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>이메일</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {user ? (
                  <Typography>{user.email}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>학번</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {user ? (
                  <Typography>{user.studentId}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>이름</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {user ? (
                  <Typography>{user.name}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>학과/학부</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {user ? (
                  <Typography>{user.department}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>전화번호</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {user ? (
                  <Typography>{user.phoneNumber}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
            </Grid>
            <Box
              sx={{
                display: 'flex',
                flexDirection: matches ? 'row' : 'column',
                justifyContent: matches ? 'flex-end' : 'center',
                alignItems: 'center',
                marginTop: 2
              }}
            >
              {isKlubSuperAdmin && (
                <Button fullWidth={!matches} onClick={handleNavigateAdmin} color="warning">
                  KLUB 어드민 관리
                </Button>
              )}
              <Button fullWidth={!matches} onClick={() => setUserDataModal(true)}>
                정보 입력 / 수정
              </Button>
              <UserDataModal
                open={userDataModal}
                setOpen={setUserDataModal}
                onClose={() => setUserDataModal(false)}
                setUser={setUser}
                nickname={user?.nickname}
                name={user?.name}
                department={user?.department}
                phoneNumber={user?.phoneNumber}
              />
              <Button fullWidth={!matches} onClick={() => setPasswordModal(true)}>
                비밀번호 수정
              </Button>
              <PasswordModal open={passwordModal} setOpen={setPasswordModal} onClose={() => setPasswordModal(false)} />
              <Button color="error" fullWidth={!matches} onClick={() => setWithdrawModal(true)}>
                회원 탈퇴
              </Button>
              <WithdrawModal open={withdrawModal} handleClose={() => setWithdrawModal(false)} />
            </Box>
          </AccordionDetails>
        </Accordion>
        <Accordion defaultExpanded={true} square={!matches} sx={{ mt: 2 }}>
          <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel-content" id="panel-header">
            <Typography variant="h4" component="h4">
              내 활동 후기
            </Typography>
          </AccordionSummary>
          <AccordionDetails id="panel-content">
            {reviews?.length ? (
              <Box
                component={Masonry}
                breakpointCols={breakpointColumns}
                sx={{
                  display: 'flex',
                  marginLeft: '-16px',
                  width: 'auto',
                  '& > div': {
                    paddingLeft: '16px',
                    backgroundClip: 'padding-box'
                  }
                }}
              >
                {reviews.map(review => (
                  <Box key={review.id} mb={2}>
                    <MyPageReviewCard {...review} onDelete={handleDeleteRequest} />
                  </Box>
                ))}
              </Box>
            ) : (
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Typography variant="body1" color="text.secondary" align="center" sx={{ py: 3 }}>
                    작성한 후기가 없습니다.
                  </Typography>
                </Grid>
              </Grid>
            )}
          </AccordionDetails>
        </Accordion>
      </Box>
      <ConfirmDialog
        open={isDeleteConfirmOpen}
        handleClose={() => setIsDeleteConfirmOpen(false)}
        handleSubmit={handleDeleteConfirm}
        confirmMessage="정말로 후기를 삭제하시겠습니까?"
      />
    </>
  )
}

export default Mypage
