import { Box, Card, Divider, Stack, Typography } from '@mui/material'

import { type ClubReview } from 'src/responses/review'

import ReviewKeyword from '../ReviewKeyword'
import SemesterChip from '../SemesterChip'

interface MyPageReviewCardProps extends ClubReview {
  onDelete: (reviewId: number, clubId: number) => void
}

const MyPageReviewCard: React.FC<MyPageReviewCardProps> = review => {
  const {
    id,
    name,
    description,
    year,
    semester,
    content,
    keywords,
    recommendedPerson,
    isHiddenByClub,
    clubId,
    onDelete
  } = review

  return (
    <Card
      sx={{
        p: '34px 36px 13px',
        borderRadius: '10px',
        boxShadow: '0px 2px 2px 0px rgba(159, 162, 191, 0.25), 0px 9px 16px 0px rgba(159, 162, 191, 0.18)',
        display: 'flex',
        flexDirection: 'column',
        gap: '14px'
      }}
    >
      <Stack direction="row" justifyContent="space-between" alignItems="flex-start">
        <Stack spacing="3px">
          {name && (
            <Typography
              sx={{
                fontFamily: 'Pretendard',
                fontSize: '13px',
                fontWeight: 400,
                color: '#000000',
                lineHeight: 1.2
              }}
            >
              {name}
            </Typography>
          )}
          {description && (
            <Typography
              sx={{
                fontFamily: 'Pretendard',
                fontSize: '14px',
                fontWeight: 400,
                color: 'rgba(34, 51, 84, 0.7)',
                lineHeight: 1.2
              }}
            >
              {description}
            </Typography>
          )}
        </Stack>
        <SemesterChip year={year} semester={semester} />
      </Stack>

      <Typography
        sx={{
          fontFamily: 'Pretendard',
          fontSize: '13px',
          lineHeight: '1.5',
          color: '#000000'
        }}
      >
        {content}
      </Typography>

      {keywords && keywords.length > 0 && (
        <Stack spacing="3px">
          {keywords.map(keyword => (
            <ReviewKeyword key={keyword.id} keyword={keyword} />
          ))}
        </Stack>
      )}

      <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ pt: 0.5 }}>
        <Typography
          sx={{
            fontFamily: 'Pretendard',
            fontSize: '13px',
            color: '#FF9393'
          }}
        >
          {recommendedPerson ? `# ${recommendedPerson}에게 추천!` : ''}
        </Typography>
      </Stack>

      <Divider sx={{ borderColor: 'rgba(159, 162, 191, 0.25)' }} />

      <Stack direction="row" justifyContent="space-between" alignItems="center">
        <Box>
          {isHiddenByClub === true && (
            <Box
              sx={{
                backgroundColor: '#B1B1B1',
                borderRadius: '16px',
                padding: '3px 7px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
              }}
            >
              <Typography
                sx={{
                  fontFamily: 'Pretendard',
                  fontSize: '13px',
                  fontWeight: 700,
                  color: '#FFFFFF'
                }}
              >
                비공개
              </Typography>
            </Box>
          )}
        </Box>
        <Box sx={{ ml: 2 }}>
          <Typography
            sx={{
              fontFamily: 'Pretendard',
              fontSize: '13px',
              fontWeight: 700,
              color: 'gray',
              cursor: 'pointer'
            }}
            onClick={() => onDelete(id, clubId)}
          >
            후기 삭제하기
          </Typography>
        </Box>
      </Stack>
    </Card>
  )
}

export default MyPageReviewCard
